.App {
  text-align: center;
  background-color: black;
  min-height: 100vh;
}

.App-background-video {
  position: absolute;
  z-index: 1;
  bottom: -20%;
  left: 0;
  /* min-height: 100vh; */
  max-width: 150vw + 100vh;
}

.App-white-title {
  position: relative;
  z-index: 2;
  margin-top: 10%;
  margin-bottom: 0px;
  padding-left: 20%;
  color: white;
  text-align: left;
  font-size: calc(3vw + 3vh);
}

.App-white-subtitle {
  position: relative;
  z-index: 2;
  margin-top: 25px;
  margin-bottom: 0px;
  padding-left: 20%;
  color: lightskyblue;
  text-align: left;
  font-size: calc(1vw + 1.5vh);
  font-weight: bolder;
}

.App-white-subtitle-2 {
  position: relative;
  z-index: 2;
  margin-top: 0px;
  margin-bottom: 0px;
  padding-left: 20%;
  color: lightskyblue;
  text-align: left;
  font-size: calc(1vw + 1.5vh);
  font-weight: bolder;
}

.App-header {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}